/****** CSS RESET ******/
/* Box sizing rules */
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
*::before, *::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* Remove default margin,padding */
body, h1, h2, h3, h4, p, ul, li, figure, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list], ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

a {
  text-decoration: none;
  line-height: 1;
}

/* Make images easier to work with */
img, picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input, button, textarea, select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  * {
    -webkit-animation-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
    animation-iteration-count: 1 !important;
    -webkit-transition-duration: 0.01ms !important;
    -o-transition-duration: 0.01ms !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  *::before, *::after {
    -webkit-animation-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
    animation-iteration-count: 1 !important;
    -webkit-transition-duration: 0.01ms !important;
    -o-transition-duration: 0.01ms !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/* CUSTOM */
a {
  color: #FFFFFF;
}

body {
  color: #FFFFFF;
  background: #333333;
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  overflow-x: hiddenq;
}

.container {
  max-width: 1440px;
  width: 90%;
  margin: 0 auto;
}

.hidden {
  display: none;
  transition: all 0.8s ease;
}

.stopScroll {
  overflow-y: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(2px);
  z-index: 5;
  transition: all 0.5s ease;
}

/* NAVBAR */
.navbar {
  margin-top: 2rem;
  height: 80px;
  width: 100%;
  z-index: 999;
}
.navbar .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar .nav .nav--hamburger {
  height: 60px;
  width: 40px;
  position: relative;
  cursor: pointer;
}
.navbar .nav .nav--hamburger .nav--hamburger__spam {
  position: absolute;
  height: 5px;
  width: 100%;
  background: #FFFFFF;
  transition: 0.5s;
}
.navbar .nav .nav--hamburger .nav--hamburger__spam:nth-child(1) {
  top: 12px;
}
.navbar .nav .nav--hamburger .nav--hamburger__spam:nth-child(2) {
  top: 24px;
}
.navbar .nav .nav--hamburger .nav--hamburger__spam:nth-child(3) {
  top: 36px;
}
.navbar .nav .nav--logo svg {
  height: 80px;
  max-height: 100%;
  width: 160px;
}
.navbar .nav .nav--search {
  background: unset;
  border: unset;
  display: flex;
  cursor: pointer;
  color: #FFFFFF;
}
.navbar .nav .nav--menu {
  z-index: 999;
  background: rgb(34, 34, 34);
  background: linear-gradient(to top, rgb(34, 34, 34) 20%, rgb(51, 51, 51) 100%);
  position: fixed;
  top: 0;
  left: -300px;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 3rem 0;
  overflow: hidden;
  width: 300px;
  transition: all 0.8s ease;
}
.navbar .nav .nav--menu.nav--menu_active {
  left: 0px;
}
.navbar .nav .nav--menu .nav--menu__close {
  background: unset;
  border: unset;
  display: flex;
  cursor: pointer;
  color: #FFFFFF;
  margin-left: auto;
  margin-right: 2rem;
  height: 2rem;
  width: 2rem;
  transition: all 0.8s ease;
}
.navbar .nav .nav--menu .nav--menu__close:hover {
  scale: 0.8;
}
.navbar .nav .nav--menu a svg {
  margin-top: 2rem;
  height: 80px;
  width: 80%;
}
.navbar .nav .nav--menu ul {
  margin-top: 5rem;
  list-style: none;
}
.navbar .nav .nav--menu ul li {
  width: 100%;
  transition: all 0.5s ease-in-out;
}
.navbar .nav .nav--menu ul li a {
  display: block;
  width: 100%;
  padding: 1rem;
}
.navbar .nav .nav--menu ul li:hover {
  transition: all 0.5s ease-in-out;
  background: rgba(255, 255, 255, 0.05);
}
.navbar .nav .nav--menu ul li:hover a {
  color: #84FAB0;
}

/* BLOG */
main {
  margin-top: 4rem;
  margin-bottom: 4rem;
}
main .container .blog--wrapper-grid {
  display: grid;
  gap: 20px;
  grid-auto-columns: 1fr;
  grid-template-areas: "one" "two" "three" "four" "five";
  /* BLOG ITEM */
}
main .container .blog--wrapper-grid .blog--item {
  position: relative;
  color: #FFFFFF;
  aspect-ratio: 16/10;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
main .container .blog--wrapper-grid .blog--item:hover .blog--item__img {
  transition: all 0.5s ease-in-out;
  filter: blur(3px);
}
main .container .blog--wrapper-grid .blog--item:hover .blog--item__text div {
  transform: scale(1.05);
  transition: all 0.5s ease-in-out;
}
main .container .blog--wrapper-grid .blog--item:hover .blog--item__text div h2 {
  color: #84FAB0;
  transition: all 0.5s ease-in-out;
}
main .container .blog--wrapper-grid .blog--item .blog--item__img {
  position: absolute;
  object-fit: cover;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
main .container .blog--wrapper-grid .blog--item .blog--item__text {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  background: rgb(0, 0, 0);
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.3) 30%, rgba(0, 0, 0, 0.1) 100%);
}
main .container .blog--wrapper-grid .blog--item .blog--item__text div {
  transition: all 0.5s ease-in-out;
  transform: scale(1);
}
main .container .blog--wrapper-grid .blog--item .blog--item__text .blog--item__tag {
  justify-self: flex-start;
  padding: 0.5rem 1rem;
  margin-right: auto;
  font-weight: bold;
  font-size: 0.875rem;
  border-radius: 0.25rem;
}
main .container .blog--wrapper-grid .blog--item .blog--item__text .blog--item__tag[data-tag=TRAVEL] {
  background: #84FAB0;
  color: #222222;
}
main .container .blog--wrapper-grid .blog--item .blog--item__text .blog--item__tag[data-tag=TECH] {
  background: #7028E4;
}
main .container .blog--wrapper-grid .blog--item h2 {
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.25;
  max-width: 15rem;
  transition: all 0.5s ease-in-out;
}
main .container .blog--wrapper-grid .blog--item p {
  margin-top: 0.5rem;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
}
main .container .blog--item:nth-child(1) {
  grid-area: one;
}
main .container .blog--item:nth-child(2) {
  grid-area: two;
  aspect-ratio: 3/4;
}
main .container .blog--item:nth-child(3) {
  grid-area: three;
}
main .container .blog--item:nth-child(4) {
  grid-area: four;
}
main .container .blog--item:nth-child(5) {
  grid-area: five;
}
main .container .blog--ads {
  background: #E5B2CA;
  background: linear-gradient(150deg, #E5B2CA 0%, #7028E4 100%);
}
main .container .blog--ads a {
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  gap: 2rem;
  color: #FFFFFF;
  padding: 1rem;
  line-height: 1.5;
}
main .container .blog--ads a img {
  width: 60%;
  margin: 0 auto;
}
main .container .blog--ads:hover {
  transform: scale(0.98);
}

/* SOCIAL ICONS*/
.social--wrapper {
  z-index: 99;
  position: absolute;
  left: 0%;
  top: 12rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background: rgba(51, 51, 51, 0.6);
  padding: 0.75rem;
  border-radius: 0.5rem;
}
.social--wrapper .social--icons {
  height: 1.5rem;
  width: 1.5rem;
  color: rgba(255, 255, 255, 0.6);
}
.social--wrapper .social--icons:hover {
  color: #FFFFFF;
}

.sticky {
  position: fixed;
  top: 0;
}

/* FOOTER */
footer {
  background: #222222;
  height: 6rem;
}
footer .container {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}
footer .container p {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
}
footer .container svg {
  height: 3rem;
  width: 5rem;
  position: relative;
  top: 0.5rem;
}

@media screen and (min-width: 420px) {
  main .container .blog--wrapper-grid .blog--item .blog--item__text {
    padding: 2rem;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 80%;
  }
  main .container .blog--wrapper-grid {
    grid-template-areas: "one one" "two three" "two four" "five five";
  }
  main .container .blog--wrapper-grid .blog--item:nth-child(1) .blog--item__text,
  main .container .blog--wrapper-grid .blog--item:nth-child(5) .blog--item__text {
    padding: 4rem;
    padding-bottom: 5rem;
    gap: 20px;
  }
  main .container .blog--wrapper-grid .blog--item:nth-child(1) .blog--item__text h2,
  main .container .blog--wrapper-grid .blog--item:nth-child(5) .blog--item__text h2 {
    font-size: 1.75rem;
    line-height: 1.4;
    max-width: 30rem;
  }
  main .container .blog--wrapper-grid .blog--item:nth-child(1) .blog--item__text p,
  main .container .blog--wrapper-grid .blog--item:nth-child(5) .blog--item__text p {
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.6);
  }
  main .container .blog--wrapper-grid .blog--item:nth-child(2) {
    grid-area: two;
    aspect-ratio: unset;
  }
  .social--wrapper {
    left: 5%;
  }
}
@media screen and (min-width: 1024px) {
  main .container .blog--wrapper-grid {
    grid-template-areas: "one one two" "one one two" "three five five" "four five five";
  }
  main .container .blog--item:nth-child(5) {
    grid-area: five;
    aspect-ratio: unset;
  }
  footer .container p {
    font-size: 1rem;
  }
}